import React, { Suspense } from 'react';
// eslint-disable-next-line import/no-extraneous-dependencies
import { Redirect, Route, Switch } from 'react-router-dom';

const List = React.lazy(() => import(/* webpackChunkName: "list" */ './list'));
const NewSource = React.lazy(() =>
  import(/* webpackChunkName: "new-source" */ './new-source')
);
const NewDestination = React.lazy(() =>
  import(/* webpackChunkName: "new-destination" */ './new-destination')
);
const NewConnection = React.lazy(() =>
  import(/* webpackChunkName: "new-connection" */ './new-connection')
);

const Connectors = ({ match }) => {
  return (
    <Suspense fallback={<div className="loading" />}>
      <Switch>
        <Route
          path={`${match.url}/list`}
          render={(props) => <List {...props} />}
        />
        <Route
          path={`${match.url}/new-source`}
          render={(props) => <NewSource {...props} />}
        />
        <Route
          path={`${match.url}/new-destination`}
          render={(props) => <NewDestination {...props} />}
        />
        <Route
          path={`${match.url}/new-connection`}
          render={(props) => <NewConnection {...props} />}
        />
        <Redirect to={`${match.url}/list`} />
      </Switch>
    </Suspense>
  );
};

export default Connectors;
